<template>
  <div class='right_frame'>
    <h3> </h3>
    <div class='info'>
      <span>// </span>
      <router-link :to='modfiyPasswordPath'></router-link>
    </div>
    <div>
      <table class="table2" width="100%">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th>IP</th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for='info in modifyInfos' :key='info.date'>
            <td>{{info.date}}</td>
            <td>{{info.time}}</td>
            <td>{{info.ip}}</td>
            <td> {{info.local}}</td>
            <td>{{info.operate}}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>
<script>
import Paths from '@/config/path.js'

export default {
  data() {
    return {
      modfiyPasswordPath: Paths.userSecuritys.userPassword,
      user: null,
      util: this.$root,
      modifyInfos: []
    };
  },
  created() {
    this.user = this.util.getUser();
    let a = {
      date: "20170725",
      time: "09:17:39",
      ip: "183.63.*.*",
      local: " ",
      operate: ""
    };
    this.modifyInfos.push(a);
  }
};
</script>
<style lang="scss" scoped>
@import "./default.scss";

.right_frame > h3 {
  font-size: 1.4em;
  font-weight: bold;
  color: #666;
}
.info {
  line-height: 1.5em;
}
</style>



