function clone1(obj) {
    if (typeof obj !== "object") {
        return obj;
    }
    if (obj instanceof Array) {
        var newobj = [];
        for (var i = 0; i < obj.length; i++) {
            newobj[i] = clone1(obj[i]);
        }
        return newobj;
    }

    var newobj = {};
    for (var attr in obj) {
        newobj[attr] = clone1(obj[attr]);
    }
    return newobj;
}

